/**
 * Avatar
 */
.o-avatar {
  width: 96px;
  height: 96px;
  color: #ffa941;
  overflow: hidden; }
  .o-avatar svg {
    width: 100%;
    fill: currentColor; }
  .o-avatar img {
    width: 100%; }

/** Sizes
--------------------------------------------------*/
.o-avatar--size-auto {
  width: 100%;
  height: 100%; }

.o-avatar--size-small {
  width: 48px;
  height: 48px; }

.o-avatar--size-large {
  width: 192px;
  height: 192px; }

/** Looks
--------------------------------------------------*/
.o-avatar--circle {
  border-radius: 100%;
  border: 1px solid #ffa941; }

/** Colors
--------------------------------------------------*/
