@font-face {
	font-family: "orange-icons";
	src: url("#{$o-font-icon-url}/orange-icons.eot");
	src: url("#{$o-font-icon-url}/orange-icons.eot") format("embedded-opentype"),
		url("#{$o-font-icon-url}/orange-icons.woff2") format("woff2"),
		url("#{$o-font-icon-url}/orange-icons.woff") format("woff"),
		url("#{$o-font-icon-url}/orange-icons.ttf") format("truetype"),
		url("#{$o-font-icon-url}/orange-icons.svg") format("svg");
	font-weight: normal;
	font-style: normal;
}

[class^="o-icon-"]::before,
[class*=" o-icon-"]::before {
	font-family: "orange-icons";
	font-style: normal;
	font-weight: normal;
	speak: none;
	display: inline-block;
	text-decoration: inherit;
	width: 1em;
	margin-right: .2em;
	text-align: center;
	font-variant: normal;
	text-transform: none;
	line-height: 1em;
	margin-left: .2em;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}


